* {
  margin: 0;
  padding: 0; }

html, body {
  height: 100%;
  background-color: lightgray; }

.grid-container {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

.unfaithfulDogs {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: navy;
  font-size: 400%;
  padding-top: 15%; }

.sentence {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: black;
  font-size: 150%; }

#title {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: lightseagreen; }
  #title h2 {
    font-size: 450%;
    padding-top: 15%; }
  #title h6 {
    font-size: 200%; }

#startOptionsGame {
  display: flex;
  justify-content: space-between; }

#hello {
  font-family: 'Berkshire Swash', cursive;
  font-size: 150%;
  text-shadow: 2px 1px white;
  border-bottom: 1px solid black;
  text-indent: 3%;
  padding-bottom: 2%; }

#startOptionsGame {
  padding: 15px 0 25px 0;
  border-bottom: 1px solid black; }

.btnInfo {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  background: linear-gradient(#FFF, #FF8C00); }

.btnLicence {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  background: linear-gradient(#FFF, #8A2BE2); }

.btnTutorial {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  background: linear-gradient(#FFF, #BDB76B); }

.btnNewGame {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  background: linear-gradient(#FFF, green); }

.btnInfo:hover, .btnLicence:hover, .btnTutorial:hover, .btnNewGame:hover {
  color: #8B4513; }

.btnInfo:active, .btnLicence:active, .btnTutorial:active, .btnNewGame:active {
  animation-name: changeBorder;
  animation-duration: 0.2s; }

@keyframes changeBorder {
  0% {
    border-radius: 15%; }
  100% {
    border-radius: 25px; } }

.descriptionMenu {
  text-indent: 3%;
  padding-top: 2%;
  text-shadow: 2px 1px white;
  font-family: 'Berkshire Swash', cursive;
  font-size: 120%; }

#chooseBtns {
  display: flex;
  justify-content: space-around; }

#randomHero {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  box-shadow: 0 9px #2F4F4F;
  background: linear-gradient(#8FBC8F, black);
  color: white;
  margin-top: 5%; }

.randomHeroDescription {
  z-index: 2;
  margin-left: 11%;
  font-family: 'Berkshire Swash', cursive;
  position: absolute; }

.createHeroDescription {
  z-index: 2;
  margin-left: 11%;
  font-family: 'Berkshire Swash', cursive;
  position: absolute; }

#createHero {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px gray;
  box-shadow: 0 9px #4B0082;
  background: linear-gradient(white, #4B0082);
  color: white;
  margin-top: 5%; }

#randomHero:hover, #createHero:hover {
  animation-name: biggerBtn;
  animation-duration: 1.75s; }

@keyframes biggerBtn {
  0% { }
  100% {
    width: 25%; } }

#slider {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  margin-top: -35px; }
  #slider ul {
    position: relative;
    margin: 0;
    padding: 0;
    height: 200px;
    list-style: none; }
    #slider ul li {
      position: relative;
      display: block;
      float: left;
      padding: 0;
      width: 1100px;
      height: 575px;
      background-color: green; }

#sliderBtn {
  display: flex;
  justify-content: space-between; }
  #sliderBtn button.control_prev {
    width: 15%;
    height: 30px;
    background: tomato;
    color: #fff;
    font-weight: bold;
    font-size: 20px; }
  #sliderBtn button.control_next {
    width: 15%;
    height: 30px;
    background: slateblue;
    color: #fff;
    font-weight: bold;
    font-size: 20px; }

.style1 {
  text-decoration: underline;
  text-decoration-color: gold;
  font-size: 22px;
  font-weight: bold; }

#chooseRace, #chooseOccupation {
  width: 100%;
  font-size: 18px;
  color: white;
  font-weight: bold;
  padding-top: 10px; }
  #chooseRace div, #chooseOccupation div {
    width: 25%;
    float: left; }
  #chooseRace #chooseRaceDescription, #chooseRace #chooseOccupationDescription, #chooseOccupation #chooseRaceDescription, #chooseOccupation #chooseOccupationDescription {
    width: 100%; }
    #chooseRace #chooseRaceDescription p:nth-child(1), #chooseRace #chooseOccupationDescription p:nth-child(1), #chooseOccupation #chooseRaceDescription p:nth-child(1), #chooseOccupation #chooseOccupationDescription p:nth-child(1) {
      color: black;
      text-decoration: underline;
      text-decoration-color: gold;
      padding: 15px;
      text-indent: 17px; }
    #chooseRace #chooseRaceDescription p:nth-child(2), #chooseRace #chooseOccupationDescription p:nth-child(2), #chooseOccupation #chooseRaceDescription p:nth-child(2), #chooseOccupation #chooseOccupationDescription p:nth-child(2) {
      width: 95%;
      text-indent: 50px;
      margin: 0 auto; }
